@import "../../assets/styles/colors";

.button {
    border-radius: 10px;
    border: 1px solid transparent;
    background-color: transparent;
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: center;
    outline: none;
    transition: .2s;
    cursor: pointer;
    font-family: "TTInterfaces";
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    flex-shrink: 0;

    &:disabled {
        background-color: $gray-300 !important;
        pointer-events: none;
    }

    &:active {
        scale: 0.95
    }
    
    &:focus {
        outline: none;
    }

    &.md {
        height: 56px;
        padding: 16px 24px;
    }
    
    &.sm {
        height: 44px;
        padding: 10px 24px;
    }

    .icon-container {
        height: 24px;
    }
    
    &.secondary {
        border-color: $blue-500;
        color: $blue-500;

        .icon-container {
            color: $red-500;
        }

        &:hover {
            border-color: $red-900;
        }
    }
    
    &.primary {
        &.blue { background-color: $blue-500 }
        &.red { background-color: $red-500 }
        color: white;

        .icon-container {
            color: white;
        }

        &.blue:hover { background-color: $blue-800 }
        &.red:hover { background-color: $red-900 }
    }

    &.fullWidth {
        width: 100%;
    }
}